<template>
  <div id="legal-pay-detail" :class="skins=='days'?'whites':'whites balck'">
    <div class="title radius4">
      <span v-if="msg.is_sure == 0">{{$t('td.nofinish')}}</span>
      <span v-if="msg.is_sure == 1">{{$t('td.finished')}}</span>
      <span v-if="msg.is_sure == 2">{{$t('td.ceiled')}}</span>
      <span v-if="msg.is_sure == 3">{{$t('td.payed')}}</span>
      <div v-if="msg.is_sure == 0">{{$t('fat.pwaitPay')}}</div>
      <div v-if="msg.is_sure == 1">{{$t('fat.odFinish')}}</div>
      <div v-if="msg.is_sure == 2">{{$t('fat.odCeil')}}</div>
      <div v-if="msg.is_sure == 3">{{$t('fat.payedWait')}}</div>
    </div>
    <div class="info ft14 radius4">
      <div>
        <span>{{$t('td.tradeTotal')}}：</span>
        <span>{{msg.deal_money || '0.000' | numFilters(3)}}</span>
      </div>
      <div class="column" v-if="msg.is_seller==0">
        <div>
          <span>{{$t('td.seller')}}:</span>
          <span v-if="msg.type=='buy'">{{user.account_number}}</span>
          <span v-else>{{msg.seller_name}}</span>
        </div>
        <div>
          <span>{{$t('fat.bank')}}:</span>
          <span>{{user.bank_name}}:{{user.bank_account}}</span>
        </div>
        <div>
          <span>{{$t('fat.alipay')}}:</span>
          <span>{{user.alipay_account}}</span>
        </div>
        <div>
          <span>{{$t('fat.wechat')}}:</span>
          <span>{{user.wechat_account}}</span>
        </div>
        <div>
          <span>{{$t('fat.realName')}}:</span>
          <span>{{user.real_name}}</span>
        </div>
      </div>
      <div>
        <span>{{$t('td.price')}}：</span>
        <span>{{msg.price || '0.000' | numFilters(3)}}</span>
      </div>
      <div>
        <span>{{$t('td.num')}}：</span>
        <span>{{msg.number || '0.000' | numFilters(3)}}{{msg.currency_name}}</span>
      </div>
      <div v-if="msg.type=='sell'">
        <span>{{$t('ctc.name')}}：</span>
        <span>{{user.real_name}}</span>
      </div>
      <div>
        <span>{{$t('td.placeTime')}}：</span>
        <span>{{msg.format_create_time}}</span>
      </div>
      <div>
        <span>{{$t('td.callWay')}}：</span>
        <span>{{msg.type=='sell'?msg.phone:msg.seller_phone}}</span>
      </div>
      <div>
        <span>{{$t('td.renum')}}：</span>
        <span>{{msg.id}}</span>
      </div>
      <div v-if="msg.is_sure == 0">
        <div
          class="pay-code flex"
          v-if="msg.user_cash_info.alipay_qr_code || msg.user_cash_info.wechat_qr_code"
        >
          <span>{{$t('fat.QRcode')}}</span>
          <p v-if="msg.user_cash_info.alipay_qr_code">
            <b>{{$t('miscro.alipayCode')}}</b>
            <img width="150" height="150" :src="msg.user_cash_info.alipay_qr_code" alt />
          </p>
          <p v-if="msg.user_cash_info.wechat_qr_code">
            <b>{{$t('miscro.wechatCode')}}</b>
            <img width="150" height="150" :src="msg.user_cash_info.wechat_qr_code" alt />
          </p>
        </div>
      </div>
      <div v-if="(msg.is_sure == 3) && (msg.type =='sell')" class="flex alcenter">
        <span>{{$t('miscro.text11')}}</span>
        <img width="150" height="150" :src="msg.pay_orders_img" @click="evimgs(msg.pay_orders_img)" alt />
      </div>
      <div
        class="red bold"
        v-if="msg.is_sure == 0 && msg.type == 'buy'"
      >{{$t('fat.please')}}{{times}}{{$t('fat.Introvert')}}{{user.real_name}}{{$t('fat.payment')}}</div>
      <div class="btns">
        <div
          class="btn cancelBtn"
          @click="showCancel = true"
          v-if="msg.is_sure == 0 && msg.type =='buy'"
        >{{$t('fat.odCeil')}}</div>

        <div
          class="btn"
          @click="showConfirm = true"
          v-if="(msg.is_sure == 3) && (msg.type =='sell')"
        >{{$t('fat.receivePays')}}</div>
        <div
          class="btn imgbtn"
          v-if="(msg.is_sure==0) && (msg.type=='buy')"
          @click="showPay = true"
        >
          {{$t('fat.imPay')}}
          <!-- <input type="file" id="file" accept="image/*" @change="file" > -->
        </div>
      </div>
    </div>
    <!-- 取消交易 -->
    <el-dialog title :visible.sync="showCancel" width="300px" center>
      <div class="transfer-content">
        <h3>{{$t('fat.tdCeil')}}</h3>
        <div class="transfer-list">
          <p>{{$t('fat.pCeil')}}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="cancel">{{$t('cuy.confirm')}}</el-button>
      </span>
    </el-dialog>

    <!-- 确认收款 -->
    <el-dialog title :visible.sync="showConfirm" width="300px" center>
      <div class="transfer-content">
        <h3>{{$t('fat.receivePay')}}</h3>
        <div class="transfer-list">
          <p>{{$t('fat.payReceive')}}</p>
          <p>{{$t('fat.badClick')}}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm">{{$t('cuy.confirm')}}</el-button>
      </span>
    </el-dialog>

    <el-dialog title :visible.sync="showPay" width="300px" center>
      <div class="transfer-content">
        <h3>{{$t('fat.paySure')}}</h3>
        <div class="transfer-list">
          <p>{{$t('fat.pdopay')}}</p>
          <p>{{$t('fat.badClick')}}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmPay">{{$t('cuy.confirm')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "",
      user: "",
      token: "",
      showConfirm: false,
      showCancel: false,
      hasPay: false,
      showPay: false,
      id: "",
      msg: {},
      src: "",
      times: "",
      skins: localStorage.getItem("skin") || "days"
    };
  },
  created() {
    var token = window.localStorage.getItem("token") || "";
    // this.id = this.$route.query.id;
    if (token) {
      this.token = token;
      this.id = this.$route.query.id || "";
      this.getMsg();
    }
  },
  methods: {
    getMsg() {
      let that = this;
      var i = layer.load();
      this.$http({
        url: "/api/legal_deal",
        params: { id: that.id },
        headers: { Authorization: that.token }
      }).then(res => {
        layer.close(i);
        if (res.data.type == "ok") {
          that.msg = res.data.message;
          that.user = res.data.message.user_cash_info;
          var time5 = res.data.message.cancel_time;

          if (res.data.message.cancel_time) {
            if (time5 - 0 < 3600) {
              setInterval(function() {
                $(".time-order").show();
                time5--;
                var time1 = parseInt(time5 / 60);
                var time2 = time5 % 60;
                if (time1 > 0 && time2 > 0) {
                  that.times =
                    time1 +
                    that.$t("fat.minute") +
                    time2 +
                    that.$t("fat.second");
                } else if (time2 > 0) {
                  that.times = time2 + that.$t("fat.second");
                } else if (time1 == 0 && time2 == 0) {
                  window.history.go(-1);
                  that
                    .$http({
                      url: "/api/handle_one",
                      method: "post",
                      data: { id: that.id },
                      headers: { Authorization: that.token }
                    })
                    .then(res => {
                      window.history.go(-1);
                    })
                    .then(() => {});
                }
              }, 1000);
            } else {
              setInterval(function() {
                $(".time-order").show();
                time5--;
                var time1 = parseInt(time5 / 60 / 60);
                var time3 = parseInt(time5 / 60) - 60 * time1;
                var time2 = time5 % 60;
                if (time1 > 0 && time2 > 0 && time3 > 0) {
                  that.times =
                    time1 +
                    that.$t("fat.hour") +
                    time3 +
                    that.$t("fat.minute") +
                    time2 +
                    that.$t("fat.second");
                } else if (time2 > 0 && time3 > 0) {
                  that.times =
                    time3 +
                    that.$t("fat.minute") +
                    time2 +
                    that.$t("fat.second");
                } else if (time2 > 0) {
                  that.times = time2 + that.$t("fat.second");
                } else if (time1 == 0 && time2 == 0 && time3 == 0) {
                  that
                    .$http({
                      url: "/api/handle_one",
                      method: "post",
                      data: { id: that.id },
                      headers: { Authorization: that.token }
                    })
                    .then(res => {
                      window.history.go(-1);
                    })
                    .then(() => {});
                }
              }, 1000);
            }
          }
        }
      });
    },
    getData() {
      var i = layer.load();
      this.$http({
        url: "/api/legal_deal",
        params: {
          id: this.id
        },
        headers: { Authorization: this.token }
      }).then(res => {
        layer.close(i);
        if (res.data.type == "ok") {
          this.msg = res.data.message;
        }
      });
    },
    cancel() {
      this.$http({
        url: "/api/user_legal_pay_cancel",
        method: "post",
        data: { id: this.id },
        headers: { Authorization: this.token }
      }).then(res => {
        layer.msg(res.data.message);
        this.showCancel = false;
        setTimeout(function() {
          location.reload();
        }, 1000);
      });
    },
    // 卖家确认付款
    confirm() {
      this.$http({
        url: "/api/legal_deal_sure",
        method: "post",
        data: { id: this.id },
        headers: { Authorization: this.token }
      })
        .then(res => {
          layer.msg(res.data.message);
          setTimeout(function() {
            location.reload();
          }, 1000);
        })
        .then(() => {
          this.showConfirm = false;
        });
    },
    file() {
      var that = this;
      var formData = new FormData();
      formData.append("file", $("#file")[0].files[0]);
      var i = layer.load();
      $.ajax({
        url: "/api/upload",
        type: "post",
        data: formData,
        processData: false,
        contentType: false,
        success: function(msg) {
          layer.close(i);
          if (msg.type == "ok") {
            that.src = msg.message;
            if (that.src) {
              that.showPay = true;
            } else {
              layer.msg(that.$t("fat.imgUp"));
            }
          }
        }
      });
    },
    // 买家确认付款n
    confirmPay() {
      this.$http({
        url: "/api/user_legal_pay",
        method: "post",
        data: { id: this.id, pay_voucher: this.src },
        headers: { Authorization: this.token }
      })
        .then(res => {
          layer.msg(res.data.message);
          setTimeout(function() {
            location.reload();
          }, 1000);
        })
        .then(() => {
          this.showPay = false;
        });
    },
    evimgs(src) {
      return layer.open({
        type: 1, //Page层类型
        area: ["100%", "100%"],
        title: "",
        shade: 0.6, //遮罩透明度
        anim: 1, //0-6的动画形式，-1不开启
        skin:'show-img',
        content: "<img src='" + src + "' alt='' class='openimg'>",
        btn: ["关闭"],
        yes: function(index) {
          layer.close(index);
        }
      });
    }
  }
};
</script>

<style lang='scss' scoped>

.whites {
  width: 1200px;
  margin: 66px auto 50px;
  background-color: #fff;
  padding-bottom: 30px;
  -webkit-box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  min-height: 630px;
  position: relative;
  top: 18px;
  > .title {
    // background: #f8f8f8;
    margin-bottom: 20px;
    padding: 0 0 20px 30px;
    span {
      font-size: 20px;
      line-height: 50px;
      font-weight: bold;
    }
    div {
      font-size: 12px;
    }
  }
  > .info {
    // background: #f8f8f8;
    padding: 0 30px;
    line-height: 40px;
    > div {
      display: flex;
    }
    span:first-child {
      width: 140px;
    }
    .column {
      div {
        display: flex;
        span:first-child {
          width: 140px;
        }
      }
    }
    > .btns {
      padding: 20px 0;
      cursor: pointer;
      > div {
        color: #fff;
        border-radius: 2px;
        padding: 0 16px;
        background: #f0b90b;
        margin-right: 30px;
        font-size: 14px;
      }
      > .cancelBtn {
        background: #ccc;
        color: #333;
      }
    }
  }
  > .cancel-box,
  > .confirm-box {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    > .content {
      margin: 200px auto 0;
      border-radius: 2px;
      width: 360px;
      background: #fff;
      line-height: 40px;
      text-align: center;
      > div:first-child {
        font-weight: 600;
      }
      > .flex {
        margin-top: 10px;
        border-top: 1px solid #cad7e0;
        cursor: pointer;
        div {
          width: 50%;
        }
        > div:first-child {
          border-right: 1px solid #cad7e0;
        }
      }
    }
  }
  .seller {
    cursor: pointer;
  }
  .pay_voucher {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
}
.openimg {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}
.imgbtn {
  position: relative;
}
#file {
  position: absolute;
  top: 0;
  left: 0;
  width: 190px;
  height: 40px;
  opacity: 0;
  z-index: 99;
  cursor: pointer;
}
.pay-code span {
  display: block;
  line-height: 190px;
}
.pay-code p {
  width: 150px;
  text-align: center;
  margin-right: 15px;
}
.dialog-footer button {
  width: 150px;
  background-color: #f0b90b;
  border: 1px solid #f0b90b;
}
.el-button:focus,
.el-button:hover {
  background-color: #f0b90b;
  border: 1px solid #f0b90b;
  color: #fff;
}
.transfer-content h3 {
  text-align: center;
  color: #f0b90b;
  font-size: 18px;
}
.transfer-list {
  margin-top: 20px;
}
.transfer-list p {
  line-height: 30px;
  font-size: 14px;
}
.dialog-footer button {
  width: 150px;
  background-color: #f0b90b;
  border: 1px solid #f0b90b;
}
.el-button:focus,
.el-button:hover {
  background-color: #f0b90b;
  border: 1px solid #f0b90b;
  color: #fff;
}
.balck {
  background-color: #1e2b34;
  color: #fff;
}
</style>



